  border: 1px solid #c6cbd1;
  border-bottom-color: #959da5;
  border-radius: 3px;
  padding: 3px 5px;
  font-size: 11px;
  line-height: 10px;
  display: inline-block;
  box-shadow: inset 0 -1px #959da5;
}

header {
  width: 270px;
  float: left;
  -webkit-font-smoothing: subpixel-antialiased;
  position: fixed;
}

ul.downloads {
  height: 40px;
  width: 270px;
  background: #f4f4f4;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  padding: 0;
  list-style: none;
}

.downloads li {
  width: 89px;
  float: left;
  height: 40px;
  border-right: 1px solid #e0e0e0;
}

.downloads li:first-child a {
  border-radius: 5px 0 0 5px;
}

.downloads li:last-child a {
  border-radius: 0 5px 5px 0;
}

.downloads a {
  color: #676767;
  text-align: center;
  height: 34px;
  padding-top: 6px;
  font-size: 11px;
  line-height: 1;
  display: block;
}

.downloads a:hover, .downloads a:focus {
  color: #675c5c;
  font-weight: bold;
}

.downloads ul a:active {
  background-color: #f0f0f0;
}

strong {
  color: #222;
  font-weight: 700;
}

.downloads li + li + li {
  width: 89px;
  border-right: none;
}

.downloads a strong {
